.footercard {
    position: fixed;
    height: calc(10vh - 2vw);
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
}

.flex-container {
    position: fixed;
    bottom: 0;
    height: 5%;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    flex-direction: column;
    
}

.social-icon {
    position: absolute;
    right: 20px;
}

@media only screen and (max-width: 650px) {
    .social-icon {
        position: relative;
        right: 0px;    
    }
}

hr {
    display: block;
    height: 1px;
    width: calc(20vw + 100px);
    border: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.5) ;
    margin: 1em 0;
    padding: 0;
}

/* slider */
.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 25px;
    background: #222222;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
}

    .slider:hover {
        opacity: 1;
    }

    .slider::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background: #ffffff;
        cursor: pointer;
    }

    .slider::-moz-range-thumb {
        width: 25px;
        height: 25px;
        background: #04AA6D;
        cursor: pointer;
    }